{\n\tdisplay: inline-flex;\n\tpadding: 0 10px;\n\tbackground-color: #eee;\n\tborder-radius: 20px;\n\tmargin-right: 10px;\n}\n\n.badge.finished {\n\tbackground-color: #49b382;\n\tcolor: #fff;\n}\n\n.badge.running {\n\tbackground-color: #dadada;\n\tcolor: #000;\n}\n\n.badge.error {\n\tbackground-color: #bd3f3f;\n\tcolor: #fff;\n}\n\n.badge.terminated {\n\tbackground-color: #f17b1b;\n\tcolor: #fff;\n}\n\n.empty-tasks-list {\n\tpadding: 20px;\n\twidth: 100%;\n\theight: 80%;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\tbox-sizing: border-box;\n\tflex-direction: column;\n}\n\n.empty-tasks-list strong {\n\tfont-size: 16px;\n\tcolor: #636363;\n}\n\n.empty-tasks-list img {\n\tmargin: 20px 0;\n}\n\nbody.theme--dark .tasks-list {\n\tbackground-color: #333333;\n}\n\nbody.theme--dark .task-row {\n\tbackground-color: #353535;\n\tborder-color: #717171;\n}\n\nbody.theme--dark .task-owner {\n\tcolor: #a9a8a8;\n}\n</style>\n"],"sourceRoot":""}]);
