___CSS_LOADER_EXPORT___.push([module.id, "\n.new-task-block[data-v-1cc9e28c] {\n\tborder: 1px solid #dadada;\n\tborder-radius: 5px;\n\tbox-shadow: 0 0 4px 0 rgba(0, 0, 0, .05);\n\tpadding: 20px;\n\tmargin: 10px;\n\twidth: 100%;\n\tmax-width: 600px;\n}\n.selection-container[data-v-1cc9e28c] {\n\twidth: 100%;\n\tdisplay: flex;\n}\n@media (max-width: 767px) {\n.selection-container[data-v-1cc9e28c] {\n\t\tflex-wrap: wrap;\n}\n}\n.block[data-v-1cc9e28c] {\n\twidth: 100%;\n\theight: 100%;\n\tmax-height: 200px;\n\toverflow-y: scroll;\n\tpadding: 10px 15px;\n\tmargin: 5px 10px;\n\tborder: 1px solid #dadada;\n\tborder-radius: 5px;\n}\n.block[data-v-1cc9e28c]:hover {\n\tbox-shadow: 0 0 10px 0 rgba(0, 0, 0, .05)\n}\n.target-directory[data-v-1cc9e28c], .custom-mask[data-v-1cc9e28c] {\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: space-between;\n\tpadding: 5px 0;\n\tborder-bottom: 1px solid #dadada;\n}\n.delete-button[data-v-1cc9e28c] {\n\tdisplay: inline-flex;\n\twidth: 15px;\n\theight: 15px;\n\tcursor: pointer;\n\tmargin: 0 10px;\n}\nbody.theme--dark .new-task-block[data-v-1cc9e28c], body.theme--dark .block[data-v-1cc9e28c] {\n\tborder-color: #717171;\n}\n", "",{"version":3,"sources":["webpack://src/components/tasks/TasksNew.vue"],"names":[],"mappings":";AAoVA;CACA,yBAAA;CACA,kBAAA;CACA,wCAAA;CACA,aAAA;CACA,YAAA;CACA,WAAA;CACA,gBAAA;AACA;AAEA;CACA,WAAA;CACA,aAAA;AACA;AAEA;AACA;EACA,eAAA;AACA;AACA;AAEA;CACA,WAAA;CACA,YAAA;CACA,iBAAA;CACA,kBAAA;CACA,kBAAA;CACA,gBAAA;CACA,yBAAA;CACA,kBAAA;AACA;AAEA;CACA;AACA;AAEA;CACA,aAAA;CACA,mBAAA;CACA,8BAAA;CACA,cAAA;CACA,gCAAA;AACA;AAEA;CACA,oBAAA;CACA,WAAA;CACA,YAAA;CACA,eAAA;CACA,cAAA;AACA;AAEA;CACA,qBAAA;AACA","sourcesContent":["<!--\n - @copyright 2021 Andrey Borysenko <andrey18106x@gmail.com>\n - @copyright 2021 Alexander Piskun <bigcat88@icloud.com>\n -\n - @author Andrey Borysenko <andrey18106x@gmail.com>\n -\n - @license GNU AGPL version 3 or any later version\n -\n - This program is free software: you can redistribute it and/or modify\n - it under the terms of the GNU Affero General Public License as\n - published by the Free Software Foundation, either version 3 of the\n - License, or (at your option) any later version.\n -\n - This program is distributed in the hope that it will be useful,\n - but WITHOUT ANY WARRANTY; without even the implied warranty of\n - MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE. See the\n - GNU Affero General Public License for more details.\n -\n - You should have received a copy of the GNU Affero General Public License\n - along with this program. If not, see <http://www.gnu.org/licenses/>.\n -\n -->\n\n<template>\n\t<div class=\"new-task-block\">\n\t\t<h2>{{ t('mediadc', 'Create a new Task') }}</h2>\n\t\t<div class=\"selection-container\">\n\t\t\t<div class=\"block target-directories-block\">\n\t\t\t\t<h3>{{ t('mediadc', 'Target directories') }}</h3>\n\t\t\t\t<div v-if=\"targetDirectoriesIds.length > 0\">\n\t\t\t\t\t<div v-for=\"fileid in targetDirectoriesIds\" :key=\"fileid\" class=\"selected-target-directories-list\">\n\t\t\t\t\t\t<div class=\"target-directory\">\n\t\t\t\t\t\t\t<span style=\"overflow-y: scroll;\">{{ targetDirectoriesPaths[fileid] }}</span>\n\t\t\t\t\t\t\t<span class=\"delete-button icon-delete\" @click=\"removeTargetDirectory(fileid)\" />\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t\t<div v-else>\n\t\t\t\t\t<span>{{ t('mediadc', 'Not selected') }}</span>\n\t\t\t\t</div>\n\t\t\t\t<br>\n\t\t\t\t<button @click=\"openDirectoriesExplorer\">\n\t\t\t\t\t<span class=\"icon-add\" />\n\t\t\t\t\t{{ t('mediadc', 'Select') }}\n\t\t\t\t</button>\n\t\t\t</div>\n\t\t\t<div class=\"block\">\n\t\t\t\t<h3>{{ t('mediadc', 'Exclude directories') }}</h3>\n\t\t\t\t<div v-if=\"excludeDirectoriesNames.length > 0\">\n\t\t\t\t\t<div v-for=\"dir in excludeDirectoriesNames\" :key=\"dir\" class=\"selected-excluded-directories-list\">\n\t\t\t\t\t\t<div class=\"target-directory\">\n\t\t\t\t\t\t\t<span style=\"overflow-y: scroll;\">{{ dir }}</span>\n\t\t\t\t\t\t\t<span class=\"delete-button icon-delete\" @click=\"removeExcludeDirectory(dir)\" />\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t\t<div v-else>\n\t\t\t\t\t<span>{{ t('mediadc', 'Not selected') }}</span>\n\t\t\t\t</div>\n\t\t\t\t<div v-if=\"Object.keys(excludeFileIds).length > 0\">\n\t\t\t\t\t<div v-for=\"fileid in excludeFileIds\" :key=\"fileid\">\n\t\t\t\t\t\t<div class=\"target-directory\">\n\t\t\t\t\t\t\t<span style=\"overflow-y: scroll;\">{{ excludeFileIds[fileid] }}</span>\n\t\t\t\t\t\t\t<span class=\"delete-button icon-delete\" @click=\"removeExcludeFileid(fileid)\" />\n\t\t\t\t\t\t</div>\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t\t<br>\n\t\t\t\t<button @click=\"openExcludeExplorer\">\n\t\t\t\t\t<span class=\"icon-add\" />\n\t\t\t\t\t{{ t('mediadc', 'Select') }}\n\t\t\t\t</button>\n\t\t\t</div>\n\t\t</div>\n\t\t<div class=\"selection-container\">\n\t\t\t<div class=\"block\">\n\t\t\t\t<h3>{{ t('mediadc', 'Custom exclude mask') }}</h3>\n\t\t\t\t<div v-if=\"customExcludeList.length > 0\" class=\"custom-masks-list\">\n\t\t\t\t\t<div v-for=\"(mask, index) in customExcludeList\" :key=\"index\" class=\"custom-mask\">\n\t\t\t\t\t\t<span>{{ mask }}</span>\n\t\t\t\t\t\t<span class=\"icon-delete\" style=\"cursor: pointer;\" @click=\"deleteCustomMask(mask)\" />\n\t\t\t\t\t</div>\n\t\t\t\t</div>\n\t\t\t\t<div v-else>\n\t\t\t\t\t<span>{{ t('mediadc', 'Not added') }}</span>\n\t\t\t\t</div>\n\t\t\t\t<div v-if=\"addingCustomMask\" style=\"display: flex; align-items: center;\">\n\t\t\t\t\t<input id=\"custom-exclude-mask\"\n\t\t\t\t\t\tref=\"customExcludeMask\"\n\t\t\t\t\t\tv-model=\"customExcludeMask\"\n\t\t\t\t\t\ttype=\"text\"\n\t\t\t\t\t\t@keyup.enter=\"addCustomMask\"\n\t\t\t\t\t\t@keyup.esc=\"cancelAddingCustomMask\">\n\t\t\t\t\t<span class=\"icon-checkmark\" style=\"width: 18px; height: 18px; margin: 0 5px; display: inline-block; cursor: pointer;\" @click=\"addCustomMask\" />\n\t\t\t\t\t<span class=\"icon-close\" style=\"width: 18px; height: 18px; margin: 0 5px; display: inline-block; cursor: pointer;\" @click=\"cancelAddingCustomMask\" />\n\t\t\t\t</div>\n\t\t\t\t<div style=\"display: flex; align-items: center; margin: 20px 0;\">\n\t\t\t\t\t<button @click=\"addNewMask\">\n\t\t\t\t\t\t<span class=\"icon-add\" />\n\t\t\t\t\t\t<span>{{ t('mediadc', 'Add mask') }}</span>\n\t\t\t\t\t</button>\n\t\t\t\t</div>\n\t\t\t</div>\n\t\t\t<div class=\"block\">\n\t\t\t\t<h3 style=\"margin: 5px 0;\">\n\t\t\t\t\t{{ t('mediadc', 'Target Mime Type') }}\n\t\t\t\t</h3>\n\t\t\t\t<select id=\"target_mtype\"\n\t\t\t\t\tv-model=\"targetMimeType\"\n\t\t\t\t\tname=\"target_mtype\">\n\t\t\t\t\t<option :value=\"0\">\n\t\t\t\t\t\t{{ t('mediadc', 'Photos') }}\n\t\t\t\t\t</option>\n\t\t\t\t\t<option :value=\"1\">\n\t\t\t\t\t\t{{ t('mediadc', 'Videos') }}\n\t\t\t\t\t</option>\n\t\t\t\t\t<option :value=\"2\">\n\t\t\t\t\t\t{{ t('mediadc', 'Photos and Videos') }}\n\t\t\t\t\t</option>\n\t\t\t\t</select>\n\t\t\t\t<h3 style=\"margin: 5px 0;\">\n\t\t\t\t\t{{ t('mediadc', 'Similarity threshold') }}\n\t\t\t\t</h3>\n\t\t\t\t<input v-model=\"similarity_threshold\"\n\t\t\t\t\ttype=\"number\"\n\t\t\t\t\tmin=\"50\"\n\t\t\t\t\tmax=\"100\"\n\t\t\t\t\tstyle=\"margin: 0 0 10px;\">\n\t\t\t</div>\n\t\t</div>\n\t\t<button v-if=\"!runningTask\"\n\t\t\tstyle=\"margin: 10px 5px 0\"\n\t\t\t:disabled=\"Object.keys(targetDirectoriesPaths).length === 0\"\n\t\t\t@click=\"runCollectorTask\">\n\t\t\tRun Collector Task\n\t\t</button>\n\t\t<button v-else disabled>\n\t\t\t<span class=\"icon-loading\" />\n\t\t</button>\n\t</div>\n</template>\n\n<script>\nimport { generateUrl } from '@nextcloud/router'\nimport { getFilePickerBuilder, showWarning, showSuccess } from '@nextcloud/dialogs'\nimport axios from '@nextcloud/axios'\nimport { requestFileInfo, getFileId, getContentType } from '../../utils/files'\nimport { mapGetters } from 'vuex'\n\nexport default {\n\tname: 'TasksNew',\n\tdata() {\n\t\treturn {\n\t\t\ttargetDirectoriesPaths: {},\n\t\t\ttargetDirectoriesIds: [],\n\t\t\texcludeDirectoriesNames: [],\n\t\t\texcludeFileIds: {},\n\t\t\ttargetMimeType: 0,\n\t\t\tsimilarity_threshold: 90,\n\t\t\tcustomExcludeList: [],\n\t\t\tcustomExcludeMask: '',\n\t\t\taddingCustomMask: false,\n\t\t\trunningTask: false,\n\t\t}\n\t},\n\tcomputed: {\n\t\t...mapGetters([\n\t\t\t'settings',\n\t\t\t'settingByName',\n\t\t\t'tasks',\n\t\t]),\n\t},\n\tbeforeMount() {\n\t\tthis.similarity_threshold = this.settingByName('similarity_threshold') !== undefined\n\t\t\t? this.settingByName('similarity_threshold').value\n\t\t\t: 90\n\t},\n\tmethods: {\n\t\tgetDirectoriesPicker(title) {\n\t\t\treturn getFilePickerBuilder(title)\n\t\t\t\t.setMultiSelect(false)\n\t\t\t\t.addMimeTypeFilter('httpd/unix-directory')\n\t\t\t\t.setModal(true)\n\t\t\t\t.setType(1)\n\t\t\t\t.allowDirectories(true)\n\t\t\t\t.build()\n\t\t},\n\t\tgetFilesPicker(title) {\n\t\t\treturn getFilePickerBuilder(title)\n\t\t\t\t.setMultiSelect(false)\n\t\t\t\t.setModal(true)\n\t\t\t\t.setType(1)\n\t\t\t\t.allowDirectories(true)\n\t\t\t\t.build()\n\t\t},\n\t\topenDirectoriesExplorer() {\n\t\t\tthis.getDirectoriesPicker(t('mediadc', 'Choose target directory')).pick().then(dir => {\n\t\t\t\tif (dir.startsWith('/')) {\n\t\t\t\t\trequestFileInfo(dir).then(res => {\n\t\t\t\t\t\tconst fileid = getFileId(res.data)\n\t\t\t\t\t\tif (fileid !== -1) {\n\t\t\t\t\t\t\tif (!(fileid in this.targetDirectoriesPaths)) {\n\t\t\t\t\t\t\t\tthis.targetDirectoriesIds.push(fileid)\n\t\t\t\t\t\t\t\tthis.targetDirectoriesPaths[fileid.toString()] = dir\n\t\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\t\tshowWarning(t('mediadc', 'This directory already selected'))\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t}\n\t\t\t\t\t})\n\t\t\t\t} else {\n\t\t\t\t\trequestFileInfo('/').then(res => {\n\t\t\t\t\t\tconst fileid = getFileId(res.data)\n\t\t\t\t\t\tif (fileid !== -1) {\n\t\t\t\t\t\t\tif (!(fileid in this.targetDirectoriesPaths)) {\n\t\t\t\t\t\t\t\tthis.targetDirectoriesIds.push(fileid)\n\t\t\t\t\t\t\t\tthis.targetDirectoriesPaths[fileid.toString()] = '/'\n\t\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\t\tshowWarning(t('mediadc', 'This directory already selected'))\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t}\n\t\t\t\t\t})\n\t\t\t\t\tthis.targetDirectoriesPaths.push('/')\n\t\t\t\t}\n\t\t\t})\n\t\t},\n\t\topenExcludeExplorer() {\n\t\t\tthis.getDirectoriesPicker(t('mediadc', 'Choose directory to exclude')).pick().then(dir => {\n\t\t\t\tif (this.excludeDirectoriesNames.findIndex(targetDir => targetDir === dir) === -1) {\n\t\t\t\t\tif (dir.startsWith('/')) {\n\t\t\t\t\t\trequestFileInfo(dir).then(res => {\n\t\t\t\t\t\t\tconst fileid = getFileId(res.data)\n\t\t\t\t\t\t\tconst contentType = getContentType(res.data)\n\t\t\t\t\t\t\tif (fileid !== -1 && contentType === '') {\n\t\t\t\t\t\t\t\t// Add directory name\n\t\t\t\t\t\t\t\tthis.excludeDirectoriesNames.push(dir.split('/')[dir.split('/').length - 1])\n\t\t\t\t\t\t\t} else if (fileid !== -1\n\t\t\t\t\t\t\t\t&& (contentType.split('/')[0] === 'image' || contentType.split('/')[0] === 'video')) {\n\t\t\t\t\t\t\t\t// Add as fileid exclude\n\t\t\t\t\t\t\t\tthis.excludeFileIds[fileid.toString()] = dir.split('/')[dir.split('/').length - 1]\n\t\t\t\t\t\t\t}\n\t\t\t\t\t\t})\n\t\t\t\t\t} else {\n\t\t\t\t\t\tthis.excludeDirectoriesNames.push('/')\n\t\t\t\t\t}\n\t\t\t\t} else {\n\t\t\t\t\tshowWarning(t('mediadc', 'This directory already excluded'))\n\t\t\t\t}\n\t\t\t})\n\t\t},\n\t\trunCollectorTask() {\n\t\t\tthis.runningTask = true\n\t\t\taxios.post(generateUrl('/apps/mediadc/api/v1/tasks/run'), {\n\t\t\t\ttargetDirectoryIds: JSON.stringify(this.targetDirectoriesIds),\n\t\t\t\texcludeList: {\n\t\t\t\t\tuser: {\n\t\t\t\t\t\tmask: [...this.excludeDirectoriesNames, ...this.customExcludeList],\n\t\t\t\t\t\tfileid: Object.keys(this.excludeFileIds),\n\t\t\t\t\t},\n\t\t\t\t\tadmin: JSON.parse(this.settingByName('exclude_list').value) || { mask: [], fileid: [] },\n\t\t\t\t},\n\t\t\t\tcollectorSettings: {\n\t\t\t\t\thashing_algorithm: JSON.parse(this.settingByName('hashing_algorithm').value) || 'phash',\n\t\t\t\t\tsimilarity_threshold: this.similarity_threshold,\n\t\t\t\t\thash_size: this.settingByName('hash_size').value || 64,\n\t\t\t\t\ttarget_mtype: this.targetMimeType,\n\t\t\t\t},\n\t\t\t}).then(res => {\n\t\t\t\tthis.runningTask = false\n\t\t\t\tif (res.data.success) {\n\t\t\t\t\tthis.$emit('update:loading', true)\n\t\t\t\t\tthis.getTasks()\n\t\t\t\t\tthis.resetForm()\n\t\t\t\t\tshowSuccess(t('mediadc', 'New task successfully created!'))\n\t\t\t\t} else {\n\t\t\t\t\tshowWarning('Some error occured while running Collector Task. Try again.')\n\t\t\t\t}\n\t\t\t})\n\t\t},\n\t\tremoveTargetDirectory(fileid) {\n\t\t\tdelete this.targetDirectoriesPaths[fileid]\n\t\t\tconst fileidIndex = this.targetDirectoriesIds.findIndex(id => id === fileid)\n\t\t\tthis.targetDirectoriesIds.splice(fileidIndex, 1)\n\t\t},\n\t\tremoveExcludeDirectory(dir) {\n\t\t\tconst dirIndex = this.excludeDirectoriesNames.findIndex(targetDir => targetDir === dir)\n\t\t\tthis.excludeDirectoriesNames.splice(dirIndex, 1)\n\t\t},\n\t\tremoveExcludeFileid(fileid) {\n\t\t\tdelete this.excludeFileIds[fileid]\n\t\t},\n\t\taddNewMask() {\n\t\t\tthis.addingCustomMask = true\n\t\t\tsetTimeout(() => {\n\t\t\t\tthis.$refs.customExcludeMask.focus()\n\t\t\t}, 100)\n\t\t},\n\t\taddCustomMask() {\n\t\t\tif (this.customExcludeMask.length > 0) {\n\t\t\t\tif (this.customExcludeList.findIndex(mask => mask === this.customExcludeMask) === -1) {\n\t\t\t\t\tthis.customExcludeList.push(this.customExcludeMask)\n\t\t\t\t\tthis.customExcludeMask = ''\n\t\t\t\t\tthis.addingCustomMask = false\n\t\t\t\t} else {\n\t\t\t\t\tshowWarning(t('mediadc', 'This mask already exists!'))\n\t\t\t\t}\n\t\t\t} else {\n\t\t\t\tshowWarning(t('mediadc', 'Enter custom mask!'))\n\t\t\t}\n\t\t},\n\t\tcancelAddingCustomMask() {\n\t\t\tthis.customExcludeMask = ''\n\t\t\tthis.addingCustomMask = false\n\t\t},\n\t\tdeleteCustomMask(mask) {\n\t\t\tconst maskIndex = this.customExcludeList.findIndex(m => m === mask)\n\t\t\tthis.customExcludeList.splice(maskIndex, 1)\n\t\t},\n\t\tasync getTasks() {\n\t\t\taxios.get(generateUrl('/apps/mediadc/api/v1/tasks')).then(res => {\n\t\t\t\tthis.$store.dispatch('setTasks', res.data)\n\t\t\t\tthis.$emit('update:loading', false)\n\t\t\t})\n\t\t},\n\t\tresetForm() {\n\t\t\tthis.targetDirectoriesPaths = {}\n\t\t\tthis.targetDirectoriesIds = []\n\t\t\tthis.excludeDirectoriesNames = []\n\t\t\tthis.excludeFileIds = {}\n\t\t\tthis.targetMimeType = 0\n\t\t\tthis.similarity_threshold = this.settingByName('similarity_threshold') !== undefined\n\t\t\t\t? this.settingByName('similarity_threshold').value\n\t\t\t\t: 90\n\t\t\tthis.customExcludeList = []\n\t\t\tthis.runningTask = false\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.new-task-block {\n\tborder: 1px solid #dadada;\n\tborder-radius: 5px;\n\tbox-shadow: 0 0 4px 0 rgba(0, 0, 0, .05);\n\tpadding: 20px;\n\tmargin: 10px;\n\twidth: 100%;\n\tmax-width: 600px;\n}\n\n.selection-container {\n\twidth: 100%;\n\tdisplay: flex;\n}\n\n@media (max-width: 767px) {\n\t.selection-container {\n\t\tflex-wrap: wrap;\n\t}\n}\n\n.block {\n\twidth: 100%;\n\theight: 100%;\n\tmax-height: 200px;\n\toverflow-y: scroll;\n\tpadding: 10px 15px;\n\tmargin: 5px 10px;\n\tborder: 1px solid #dadada;\n\tborder-radius: 5px;\n}\n\n.block:hover {\n\tbox-shadow: 0 0 10px 0 rgba(0, 0, 0, .05)\n}\n\n.target-directory, .custom-mask {\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: space-between;\n\tpadding: 5px 0;\n\tborder-bottom: 1px solid #dadada;\n}\n\n.delete-button {\n\tdisplay: inline-flex;\n\twidth: 15px;\n\theight: 15px;\n\tcursor: pointer;\n\tmargin: 0 10px;\n}\n\nbody.theme--dark .new-task-block, body.theme--dark .block {\n\tborder-color: #717171;\n}\n</style>\n"],"sourceRoot":""}]);
