	background-color: #fff;
	border-radius: 5px;
	margin-bottom: 10px;
	transition: box-shadow .3s;
}

.task-row:hover {
	box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
}

.task-row:hover .delete-task-button {
	visibility: visible;
}

.task-row a {
	padding: 10px 0 10px 20px;
	width: 100%;
	height: 100%;
	display: inline-flex;
	align-items: center;
}

.delete-task-button {
	display: inline-flex;
	visibility: hidden;
	width: 16px;
	height: 16px;
	margin: 10px 20px;
	cursor: pointer;
}

.task-owner {
	color: #585858;
}

@media (max-width: 540px) {
	.task-row, .task-row a {
		flex-direction: column;
	}
	.delete-task-button {
		visibility: visible;
	}
	.badge {
		margin-right: 0;
